.schedule-day {
    margin-bottom: 30px;
  }
  
  .day-title {
    /*background: var(--primary);*/
    color: white;
    padding: 10px 15px;
    border-radius: 8px 8px 0 0;
  }
  
  .events-container {
    background: white;
    border-radius: 0 0 8px 8px;
    padding: 15px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  }
  
  .event-card {
    display: flex;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #eee;
  }
  
  .event-time {
    min-width: 100px;
    font-weight: bold;
    color: var(--primary);
  }
  
  .event-details {
    flex: 1;
  }
  
  .event-title {
    margin-bottom: 5px;
  }
  
  .event-type {
    font-size: 0.9rem;
    color: #666;
    margin-bottom: 5px;
  }
  
  .event-responsible {
    font-size: 0.9rem;
    color: #444;
  }
  
  .event-course {
    font-weight: bold;
    margin-top: 5px;
  }