.exhibition-card {
    padding:5px ;
    background: white;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  
  .exhibition-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
  
  .exhibition-header {
    padding: 15px;
    display: flex;
    align-items: center;
    background: var(--primary);
    color: white;
  }
  
  .exhibition-logo {
    width: 40px;
    height: 40px;
    margin-right: 15px;
    object-fit: contain;
  }
  
  .stand-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .exhibition-info {
    padding: 15px;
  }
  
  .stand-number {
    font-weight: bold;
    color: var(--primary);
  }
  
  .course {
    display: inline-block;
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 0.8rem;
    margin: 5px 0;
  }
  
  .description {
    margin-top: 10px;
    color: #555;
  }

  .preview-btn {
    background: #102464;
    color: #fff; /* Cor do texto */
    border: none; /* Remove bordas padrão */
    border-radius: 25px; /* Bordas arredondadas */
    padding: 10px 20px; /* Espaçamento interno */
    font-size: 1rem; /* Tamanho do texto */
    font-weight: bold; /* Texto em negrito */
    cursor: pointer; /* Mostra o cursor de clique */
    transition: all 0.3s ease; /* Animação suave */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Sombra para destacar */
    margin: 10px;
  }
  
  .preview-btn:hover {
    background: linear-gradient(45deg, #2575fc, #102464); /* Inverte o gradiente no hover */
    transform: scale(1.05); /* Aumenta ligeiramente o botão */
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3); /* Aumenta a sombra no hover */
  }